<template>
    <div id="AdminMessage">
        <el-table
          :data="tableData"
          border
          style="width: 100%"
          >
          <el-table-column
            prop="_id"
            label="id"
            >
          </el-table-column>
          <el-table-column
            prop="name"
            label="姓名"
            >
          </el-table-column>
         <el-table-column
            prop="email"
            label="邮箱"
            >
          </el-table-column>
          <el-table-column
            prop="des"
            label="留言"
            >
          </el-table-column>
          <el-table-column
            prop="time"
            :formatter="fmtTime"
            label="时间"
            >
          </el-table-column>
        </el-table>
    </div>
</template>

<script>
export default {
    name: 'Adminmessage',

    data() {
        return {
            tableData: []
        };
    },
    mounted() {
        this.getMessage()
    },
    methods: {
        async getMessage(){
          let {data} = await this.$axios({
            method: "GET",
            url: "/admin/contact",
          })
          if(data.code !== 0){
            return this.$message.error("参数错误")
          }
          this.tableData = data.data
        },
        fmtTime(row, column, cellValue, index){
            let date = new Date(cellValue)
            let YY = date.getFullYear()
            let MM = date.getMonth() + 1
            let DD = date.getDate()
            let hh = date.getHours()
            let mm = date.getMinutes()
            let ss = date.getSeconds()

            // 双数字处理
              MM = (MM < 10 ? "0" : "") + MM;
              DD = (DD < 10 ? "0" : "") + DD;
              hh = (hh < 10 ? "0" : "") + hh;
              mm = (mm < 10 ? "0" : "") + mm;
              ss = (ss < 10 ? "0" : "") + ss;

              return `${YY}-${MM}-${DD}  ${hh}:${mm}:${ss}`
        },

    },

};
</script>

<style lang="scss" scoped>

</style>