<template>
    <div id="AdminArticleManger">
        <el-table
          :data="tableData"
          border
          style="width: 100%"
          >
          <el-table-column
            prop="_id"
            label="id"
            >
          </el-table-column>
          <el-table-column
            prop="title"
            label="标题"
            >
          </el-table-column>
         <el-table-column
            prop="time"
            :formatter="fmtTime"
            label="时间"
            >
          </el-table-column>
          <el-table-column
            prop="pv"
            label="浏览量"
            >
          </el-table-column>
           <el-table-column label="操作">
               <template slot-scope="scope">
                   <el-popconfirm
                      confirm-button-text='好的'
                      cancel-button-text='不用了'
                      icon="el-icon-info"
                      icon-color="red"
                      title="确定删除吗？"
                      @confirm="handleDelete(scope.$index, scope.row)"
                    >
                      <el-button
                            slot="reference"
                             size="mini"
                             type="danger"
                             >删除</el-button>
                    </el-popconfirm>


                  
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
export default {
    name: 'AdminArticleManger',
    data() {
        return {
            tableData: []
        };
    },
    mounted() {
        this.getArticle()
    },
    methods: {
        // 请求文章数据
        async getArticle(){
            let res = await this.$axios({
                method: "POST",
                url: "/admin/getArticle"
            })
            this.tableData = res.data.data
        },
        // 表格时间格式化

        fmtTime(row, column, cellValue, index){
            let date = new Date(cellValue)
            let YY = date.getFullYear()
            let MM = date.getMonth() + 1
            let DD = date.getDate()
            let hh = date.getHours()
            let mm = date.getMinutes()
            let ss = date.getSeconds()

            // 双数字处理
              MM = (MM < 10 ? "0" : "") + MM;
              DD = (DD < 10 ? "0" : "") + DD;
              hh = (hh < 10 ? "0" : "") + hh;
              mm = (mm < 10 ? "0" : "") + mm;
              ss = (ss < 10 ? "0" : "") + ss;

              return `${YY}-${MM}-${DD}  ${hh}:${mm}:${ss}`
        },
        async handleDelete(index, row){
            let {data} = await this.$axios({
                method: "delete",
                url: "/admin/article",
                data: {
                    _id: row._id
                    }
            })
            if(data.code !== 0){
                return this.$message.error("参数错误")
            }
            // 删除数据成功
             this.$message({
                type: "success",
                duration: 1000,
                message: data.msg,
            })
            // 更新数据
             this.getArticle()
            //  事件车更新hotArticle模块内容
            this.$bus.$emit("articleUpdata");

        }
    },
};
</script>

