<template>
    <div id="AdminArticleAdd">
        <h2>添加友链</h2>
        <el-form :model="form">
          <el-form-item label="名称">
            <el-input v-model="form.name"></el-input>
          </el-form-item>
          <el-form-item label="链接">
            <el-input v-model="form.src"></el-input>
          </el-form-item>
          <el-form-item label="图标链接">
            <el-input v-model="form.icon"></el-input>
          </el-form-item>
          <el-form-item label="内容">
            <el-input v-model="form.des"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" round @click="handleSubmit">主要按钮</el-button>
          </el-form-item>

          
        </el-form>
    </div>
</template>

<script>
export default {
    name: 'Adminarticleadd',
    data() {
        return {
            form: {
                name: "",
                src: "",
                icon: "",
                des: ""
            }
        };
    },
    mounted() {
        
    },
    methods: {
        async handleSubmit(){
            let data = await this.$axios({
                method: "POST",
                url: "/admin/addLink",
                data: {
                    src: this.form.src,
                    name: this.form.name,
                    icon: this.form.icon,
                    des: this.form.des
                }

            })
            if(data.data.code !== 0){
                return this.$message.error("参数错误")
            }
            this.$message({
                type: "success",
                duration: 1000,
                message: data.data.msg
            })
            this.$router.push("/Links")
        }
    },
};
</script>

<style lang="scss" scoped>

</style>