<template>
 <div id="Admin">
     <div class="no" v-if="!isAdmin">
         抱歉，您登录的不是管理员账号
         <router-link to="/">返回首页</router-link>
     </div>
     <div class="yes" v-else>
         <el-tabs :tab-position="tabPosition">
           <el-tab-pane label="文章发表">
               <AdminArticle></AdminArticle>
           </el-tab-pane>
           <el-tab-pane label="文章管理">
               <AdminArticleManger></AdminArticleManger>
           </el-tab-pane>
           <el-tab-pane label="添加友链">
               <AdminArticleAdd></AdminArticleAdd>
           </el-tab-pane>
           <el-tab-pane label="友链管理">
               <AdminLinkManger></AdminLinkManger>
           </el-tab-pane>
           <el-tab-pane label="留言管理">
               <AdminMessage></AdminMessage>
           </el-tab-pane>
         </el-tabs>
     </div>
 </div>
</template>

<script>

import AdminArticle from "@/components/Admin/AdminArticle.vue"
import AdminArticleManger from "@/components/Admin/AdminArticleManger.vue"
import AdminArticleAdd from "@/components/Admin/AdminArticleAdd.vue"
import AdminLinkManger from "@/components/Admin/AdminLinkManger.vue"
import AdminMessage from "@/components/Admin/AdminMessage.vue"
 export default {
    name: "Admin",
   data () {
     return {
         isAdmin: false,
         tabPosition: "top"
     }
   },
   components: {
       AdminArticle,AdminArticleManger,AdminArticleAdd,AdminLinkManger,AdminMessage
   },
   async created() {
       let {data} = await this.$axios({
           method: "POST",
           url: "/admin",
       })
       this.isAdmin = data.code === 0
   },
 }
</script>
<style lang="less" scoped>
    #Admin{
        .yes{
            background-color: #fff;
            padding: 20px;
            height: 500px;
        }
    }
</style>
