<template>
    <div id="AdminLinkManger">
        <el-table
          :data="tableData"
          border
          style="width: 100%"
          >
          <el-table-column
            prop="_id"
            label="_id"
            >
          </el-table-column>
          <el-table-column
            prop="name"
            label="名称"
            >
          </el-table-column>
         <el-table-column
            prop="src"
            label="链接"
            >
          </el-table-column>
          <el-table-column
            prop="des"
            label="描述"
            >
          </el-table-column>
           <el-table-column label="操作">
               <template slot-scope="scope">
                   <el-popconfirm
                      confirm-button-text='好的'
                      cancel-button-text='不用了'
                      icon="el-icon-info"
                      icon-color="red"
                      title="确定删除吗？"
                      @confirm="handleDelete(scope.$index, scope.row)"
                    >
                      <el-button
                            slot="reference"
                             size="mini"
                             type="danger"
                             >删除</el-button>
                    </el-popconfirm>


                  
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
export default {
    name: 'Adminlinkmanger',
    data() {
        return {
            tableData: []
        };
    },
    mounted() {
        this.getLink()
    },
    methods: {
        async getLink(){
            let res = await this.$axios({
                method: "GET",
                url: "/link",
                
            })
            this.tableData = res.data.data
        },
        async handleDelete(idnex, row){
            let {data} = await this.$axios({
                method: "delete",
                url: "/admin/delLink",
                data: {_id: row._id}
            })
            if(data.code !== 0){
                return this.$message.error("参数错误")
            }

            this.$message({
                type: "success",
                duration: 1000,
                message: data.msg
            })
            this.getLink()
        }
    },
};
</script>

<style lang="scss" scoped>

</style>