<template>
 <div id="AdminArticle">
     <h2>文章发表</h2>
     <el-form :label-position="labelPosition" label-width="80px" :model="formLabelAlign">
       <el-form-item label="标题">
         <el-input v-model="formLabelAlign.title"></el-input>
       </el-form-item>
       <el-form-item label="内容">
         <el-input v-model="formLabelAlign.des"></el-input>
       </el-form-item>
       <el-form-item label="md上传">
         <el-upload
          class="md-upload"
          ref="mdUpload"
          name="md"
          :action="urlBase+'admin/md'"
          :on-success="mdUploadSuccess"
          
          :auto-upload="false">
          
          <el-button
            slot="trigger"
            size="small"
            type="primary"
          >选取文件</el-button>
          <el-button
            style="margin-left: 10px;"
            size="small"
            type="success"
            @click="submitMdUpload"
          >上传到服务器
          </el-button>
          <div slot="tip" class="el-upload__tip">请上传.md格式的文件</div>
        </el-upload>
       </el-form-item>
       <el-form-item>
        <el-button
          type="primary"
          @click="submitForm('form')"
        >提交</el-button>
      </el-form-item>
     </el-form>
 </div>
</template>

<script>
 export default {
   data () {
     return {
         urlBase: process.env.NODE_ENV === "development"?"http://localhost:3000/":"",
         labelPosition: "top",
        
         formLabelAlign: {
             title: '',
             des: '',
             src: ''
         },
     }
   },
   methods: {
       //    提交信息
        async submitForm(formName){
            let {data} = await this.$axios({
                method: "POST",
                url: "/admin/article",
                data: {
                    title: this.formLabelAlign.title,
                    des: this.formLabelAlign.des,
                    src: this.formLabelAlign.src
                }
            })
            if(data.code !== 0){
              console.log(data.msg)
                return this.$message.error("参数错误")
            }
            this.$message({
              type: "success",
              duration: 1000,
              message: data.msg,
              
            })
            this.$router.push("/Article/"+data._id)

        },
    //    将md文件上传到服务器
       submitMdUpload(){
           this.$refs.mdUpload.submit()
        //    this.$refs.mdUpload.submit()
       },
    
    //  上传成功的钩子
        mdUploadSuccess(res, file, fileList){
            if(res.code !== 0){
                return this.$message.error(res.msg)
            }
            this.$message({
                type: "success",
                message: res.msg,
                duration: 1000,
            })
            
            this.formLabelAlign.src = res.mdSrc
        } 
   },
 }
</script>

